"use client"

import { useState } from "react"
import { Filter, Search, X } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import {
  Drawer,
  DrawerClose,
  DrawerContent,
  DrawerFooter,
  DrawerHeader,
  DrawerTitle,
  DrawerTrigger,
} from "@/components/ui/drawer"
import type { FilterOptions } from "@/lib/types"

interface FiltersProps {
  filters: FilterOptions
  onFiltersChange: (filters: FilterOptions) => void
  neighborhoods: string[]
}

export function Filters({ filters, onFiltersChange, neighborhoods }: FiltersProps) {
  const [isOpen, setIsOpen] = useState(false)
  const [localFilters, setLocalFilters] = useState(filters)

  const handleApplyFilters = () => {
    onFiltersChange(localFilters)
    setIsOpen(false)
  }

  const handleClearFilters = () => {
    const clearedFilters: FilterOptions = {
      search: "",
      minPrice: null,
      maxPrice: null,
      bedrooms: null,
      propertyType: "all",
      neighborhood: "",
    }
    setLocalFilters(clearedFilters)
    onFiltersChange(clearedFilters)
  }

  const activeFiltersCount = [
    filters.minPrice,
    filters.maxPrice,
    filters.bedrooms,
    filters.propertyType !== "all" ? filters.propertyType : null,
    filters.neighborhood,
  ].filter(Boolean).length

  return (
    <div className="flex flex-col gap-3 px-4 py-4">
      {/* Search bar - always visible */}
      <div className="relative">
        <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
        <Input
          placeholder="Buscar por endereço, bairro..."
          value={filters.search}
          onChange={(e) => onFiltersChange({ ...filters, search: e.target.value })}
          className="min-h-[44px] pl-10 bg-secondary border-border"
        />
      </div>

      {/* Filter drawer trigger */}
      <Drawer open={isOpen} onOpenChange={setIsOpen}>
        <DrawerTrigger asChild>
          <Button variant="outline" className="min-h-[44px] w-full justify-between border-border bg-secondary hover:bg-primary-foreground/90 hover:text-secundary-foreground">
            <span className="flex items-center gap-2">
              <Filter className="h-4 w-4" />
              Filtros
            </span>
            {activeFiltersCount > 0 && (
              <span className="flex h-5 w-5 items-cen ter justify-center rounded-full bg-primary text-xs font-medium text-primary-foreground">
                {activeFiltersCount}
              </span>
            )}
          </Button>
        </DrawerTrigger>

        <DrawerContent className="bg-card">
          <DrawerHeader className="border-b border-border">
            <DrawerTitle className="text-foreground">Filtrar Imóveis</DrawerTitle>
          </DrawerHeader>

          <div className="flex flex-col gap-6 p-4">
            {/* Property Type */}
            <div className="flex flex-col gap-2">
              <Label className="text-foreground">Tipo</Label>
              <Select
                value={localFilters.propertyType}
                onValueChange={(value: "all" | "sale" | "rent") =>
                  setLocalFilters({ ...localFilters, propertyType: value })
                }
              >
                <SelectTrigger className="min-h-[44px] bg-secondary border-border">
                  <SelectValue placeholder="Todos" />
                </SelectTrigger>
                <SelectContent className="bg-card border-border">
                  <SelectItem value="all">Todos</SelectItem>
                  <SelectItem value="sale">Venda</SelectItem>
                  <SelectItem value="rent">Aluguel</SelectItem>
                </SelectContent>
              </Select>
            </div>

            {/* Price Range */}
            <div className="flex flex-col gap-2">
              <Label className="text-foreground">Faixa de Preço</Label>
              <div className="flex gap-2">
                <Input
                  type="number"
                  placeholder="Mínimo"
                  value={localFilters.minPrice ?? ""}
                  onChange={(e) =>
                    setLocalFilters({
                      ...localFilters,
                      minPrice: e.target.value ? Number(e.target.value) : null,
                    })
                  }
                  className="min-h-[44px] bg-secondary border-border"
                />
                <Input
                  type="number"
                  placeholder="Máximo"
                  value={localFilters.maxPrice ?? ""}
                  onChange={(e) =>
                    setLocalFilters({
                      ...localFilters,
                      maxPrice: e.target.value ? Number(e.target.value) : null,
                    })
                  }
                  className="min-h-[44px] bg-secondary border-border"
                />
              </div>
            </div>

            {/* Bedrooms */}
            <div className="flex flex-col gap-2">
              <Label className="text-foreground">Quartos</Label>
              <Select
                value={localFilters.bedrooms?.toString() ?? "any"}
                onValueChange={(value) =>
                  setLocalFilters({
                    ...localFilters,
                    bedrooms: value === "any" ? null : Number(value),
                  })
                }
              >
                <SelectTrigger className="min-h-[44px] bg-secondary border-border">
                  <SelectValue placeholder="Qualquer" />
                </SelectTrigger>
                <SelectContent className="bg-card border-border">
                  <SelectItem value="any">Qualquer</SelectItem>
                  <SelectItem value="1">1+</SelectItem>
                  <SelectItem value="2">2+</SelectItem>
                  <SelectItem value="3">3+</SelectItem>
                  <SelectItem value="4">4+</SelectItem>
                </SelectContent>
              </Select>
            </div>

            {/* Neighborhood */}
            <div className="flex flex-col gap-2">
              <Label className="text-foreground">Bairro</Label>
              <Select
                value={localFilters.neighborhood || "all"}
                onValueChange={(value) =>
                  setLocalFilters({
                    ...localFilters,
                    neighborhood: value === "all" ? "" : value,
                  })
                }
              >
                <SelectTrigger className="min-h-[44px] bg-secondary border-border">
                  <SelectValue placeholder="Todos os bairros" />
                </SelectTrigger>
                <SelectContent className="bg-card border-border">
                  <SelectItem value="all">Todos os bairros</SelectItem>
                  {neighborhoods.map((neighborhood) => (
                    <SelectItem key={neighborhood} value={neighborhood}>
                      {neighborhood}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </div>

          <DrawerFooter className="border-t border-border">
            <div className="flex gap-2">
              <Button
                variant="outline"
                onClick={handleClearFilters}
                className="min-h-[44px] flex-1 border-border bg-transparent hover:bg-primary-foreground/90 hover:text-secundary-foreground"
              >
                <X className="mr-2 h-4 w-4" />
                Limpar
              </Button>
              <Button onClick={handleApplyFilters} className="min-h-[44px] flex-1 bg-primary text-primary-foreground">
                Aplicar Filtros
              </Button>
            </div>
            <DrawerClose asChild>
              <Button variant="ghost" className="min-h-[44px] border bg-transparent hover:bg-primary-foreground/90 hover:text-secundary-foreground">
                Cancelar
              </Button>
            </DrawerClose>
          </DrawerFooter>
        </DrawerContent>
      </Drawer>
    </div>
  )
}
